.loader {
  width: 44px;
  height: 44px;
  transition: all .2s;
  @include absCentering;

  &:before {
    content: '';
    width: 6px;
    height: 6px;
    border-radius: 50%;
    position: absolute;
    display: block;
    background: $color-1;
    top: 37px;
    left: 19px;
    transform: translate(-18px, -18px);
    animation: dotRect 3s cubic-bezier(0.785, 0.135, 0.15, 0.86) infinite;
  }

  .svg-loader {
    display: block;
    width: 100%;
    height: 100%;

    rect {
      fill: none;
      stroke: #2F3545;
      stroke-width: 10px;
      stroke-linejoin: round;
      stroke-linecap: round;
      stroke-dasharray: (256 / 4 * 3) (256 / 4) (256 / 4 * 3) (256 / 4);
      stroke-dashoffset: 0;
      animation: pathRect 3s cubic-bezier(0.785, 0.135, 0.15, 0.86) infinite;
    }
  }
}

// ANIMATION
@keyframes pathRect {
  25% {
    stroke-dashoffset: 64;
  }

  50% {
    stroke-dashoffset: 128;
  }

  75% {
    stroke-dashoffset: 192;
  }

  100% {
    stroke-dashoffset: 256;
  }
}

@keyframes dotRect {
  25% {
    transform: translate(0, 0);
  }

  50% {
    transform: translate(18px, -18px);
  }

  75% {
    transform: translate(0, -36px);
  }

  100% {
    transform: translate(-18px, -18px);
  }
}

// OVERLAY
.overlay {
  height: 100%;
  width: 100%;
  background-image: linear-gradient(to right, #201c2d 0%, #29323c 100%);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  transition: all .8s;
}


// SCROLLOUT
[data-scroll] {
  transition: opacity 1s;
}

[data-scroll="in"] {
  opacity: 1;
}

[data-scroll="out"] {
  opacity: 0;
}